*{
    box-sizing: border-box;
}
.bg{
    background: -webkit-linear-gradient(top, rgb(76, 175 ,50), rgb(174,213, 81));
    background: linear-gradient(to bottom, rgb(76, 175 ,50), rgb(174,213, 81))
}

.bg-fosco{
    background-color: #FFFFFF22;
}

.bg-botao{
    background-color: #FFF;
    box-shadow: var(--shadow);
}


nav.navbar{
    box-shadow: var(--shadow);
}

.nav-link{
    cursor: pointer;
}
.block{
    display: block
}
.shadow{
    box-shadow: var(--shadow);
}

.left{
    overflow: hidden;
}

.right{
    overflow-y: scroll;
    height: 80vh;
}

